article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: 01C 01D 018 019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button],
input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

/* normalize, clearfix, border-box, anti-aliasing, grey-scale */
@font-face {
  font-family: 'Libre-Baskerville-Reg';
  font-display: fallback;
  font-weight: normal;
  font-style: normal;
  src: local("Libre Baskerville"), local("LibreBaskerville-Regular"), url("fonts/libre-baskerville-v5-latin-regular.woff2") format("woff2"), url("fonts/libre-baskerville-v5-latin-regular.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */ }

html {
  font-size: 62.5%; }

body {
  font-family: sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  color: #09090e; }

h1 {
  font-family: "Libre-Baskerville-Reg", Fallback, "Georgia", serif, serif;
  font-size: 1.7rem;
  font-weight: 400; }

*::selection {
  background: #fff0f5; }

html,
body {
  height: 100%; }

body {
  width: 100%;
  min-height: 100%;
  margin: 0; }

.wrapper {
  max-width: 100%; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

p {
  margin: 0; }

a {
  text-decoration: none;
  color: #2c638f; }

img {
  display: block; }

ul {
  padding: 0;
  list-style: none; }

header {
  background: #fafafa; }

.nav {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #d7d7d7; }
  .nav .link:hover {
    color: #1a3b55; }
  .nav .logo-link {
    padding: 10px 10px;
    margin-right: 10px;
    border-right: 1px solid #d7d7d7; }
    .nav .logo-link:hover {
      background: #fff0f5; }
  .nav .logo-img {
    width: 25px;
    height: 25px; }
